import { Link } from "react-router-dom";
import blog from "../images/basic-blog.png";
import tic from "../images/tic-tac-toe.png";

export default function Home() {
    return (
        <main style={{ padding: "1rem 0" }}>
            <div className="container mt-3">
                <div className="row">
                    <div className="col-md-8 offset-md-2 p-3">
                        <div id="carouselIndicators" class="carousel slide carousel-dark" data-bs-ride="carousel">
                            <div class="carousel-indicators">
                                <button type="button" data-bs-target="#carouselIndicators" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
                                <button type="button" data-bs-target="#carouselIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                            </div>
                            <div class="carousel-inner">
                                <div class="carousel-item active">
                                    <img src={blog} class="d-block w-100" alt="blog" />
                                    <div className="carousel-caption d-none d-md-block">
                                        <button type="button" class="btn btn-link btn-md"><Link to="/projects">View</Link></button>
                                    </div>
                                </div>
                                <div class="carousel-item">
                                    <img src={tic} class="d-block w-100" alt="tic" />
                                    <div className="carousel-caption d-none d-md-block">
                                        <button type="button" class="btn btn-link btn-md"><Link to="/projects">View</Link></button>
                                    </div>
                                </div>
                            </div>
                            <button class="carousel-control-prev" type="button" data-bs-target="#carouselIndicators" data-bs-slide="prev">
                                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span class="visually-hidden">Previous</span>
                            </button>
                            <button class="carousel-control-next" type="button" data-bs-target="#carouselIndicators" data-bs-slide="next">
                                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                <span class="visually-hidden">Next</span>
                            </button>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="offset-md-1 col-md-10 p-3">
                        <div class=" custom-bg p-2 rounded">
                            <div class="card-body">
                                <p className="card-text text-center">I am passionate about technology and developing. I am currently looking for entry level software positions.</p>
                                <Link className="text-center" to="/about">Learn more about me and my skills</Link>
                                <br />
                                <Link className="text-center" to="/contact">Contact me</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}