import React from "react";
import { Link } from "react-router-dom";




export default function Navigation() {
    return (
        <header className="secondary-bg fixed-top">
            <nav class="navbar navbar-expand-lg" aria-label="navbar">
                <div class="container-fluid">
                    <button class="navbar-toggler custom-color" type="button" data-bs-toggle="collapse" data-bs-target="#navbar" aria-controls="navbar" aria-expanded="false" aria-label="Toggle navigation">
                        <i class="fas fa-bars custom-color"></i>
                    </button>

                    <div class="collapse navbar-collapse justify-content-md-center" id="navbar">
                        <ul class="navbar-nav">
                            <li class="nav-item">
                                <Link class="nav-link" data-toggle="tab" to="/">Home</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" data-toggle="tab" to="/projects">Projects</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" data-toggle="tab" to="/about">About</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" data-toggle="tab" to="/contact">Contact</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </header>
    )
}