import React, { useRef } from "react";
import emailjs, { init } from 'emailjs-com';
init(process.env.REACT_APP_USER_ID);
require('dotenv').config();
export default function Form() {
    const form = useRef();

    const sendEmail = (e) => {
        console.log("submitting form");
        e.preventDefault();
        console.log(form.current);
        console.log(e.target);


        emailjs.sendForm(process.env.REACT_APP_SERVICE_ID, process.env.REACT_APP_TEMPLATE_ID, form.current, process.env.REACT_APP_USER_ID)
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset();
    }

    return (
        <form ref={form} onSubmit={sendEmail}>
            <div class="mb-3">
                <label for="formControlInput1" class="form-label">Name</label>
                <input type="text" name="from_name" class="form-control" id="formControlInput1" placeholder="Your Name" />
            </div>
            <div class="mb-3">
                <label for="formControlInput1" class="form-label">Email address</label>
                <input type="email" name="email" class="form-control" id="formControlInput1" placeholder="name@example.com" />
            </div>
            <div class="mb-3">
                <label for="formControlTextarea1" class="form-label">Message</label>
                <textarea class="form-control" name="message" id="formControlTextarea1" rows="3"></textarea>
            </div>
            <button className="btn btn-light secondary-bg">Send Email</button>
        </form>
    )
}