
import blog from "../images/basic-blog.png";
import tic from "../images/tic-tac-toe.png";
export default function Projects() {
    return (
        <main style={{ padding: "1rem 0" }}>
            <div className="container mt-3">
                <h3 className="text-center">Projects</h3>
                <div class="row p-3">
                        <div class="col-md-6 p-2">
                            <div class="card shadow-sm">
                                <img class="card-img-top" role="img" alt="basic-blog" src={blog}></img>
                                <div class="card-body">
                                    <h5 class="card-title">Music Blog</h5>
                                    <p>This is an example blog site bout music using Node.js, Expess.js, MongoDB, Mongoose, EJS, Passport and Bootstrap.</p>
                                    <div class="d-flex justify-content-between align-items-center">
                                        <div class="btn-group">
                                            <button type="button" class="btn btn-sm btn-outline-secondary"><a href="https://github.com/stevensantos2/basic-blog" target="_blank">Github</a></button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-md-6 p-2">
                            <div class="card shadow-sm">
                                <img class="card-img-top" role="img" alt="tic-tac-toe" src={tic}></img>
                                <div class="card-body">
                                    <h5 class="card-title">Tic Tac Toe</h5>
                                    <p>This is a basic game that uses React's functional components, props and hooks. Deployed on AWS Amplify.</p>
                                    <div class="d-flex justify-content-between align-items-center">
                                        <div class="btn-group">
                                            <button type="button" class="btn btn-sm btn-outline-secondary"><a href="https://github.com/stevensantos2/tic-tac-toe" target="_blank">Github</a></button>
                                            <button type="button" class="btn btn-sm btn-outline-secondary"><a href="https://main.dswbw3j3h6mu1.amplifyapp.com/" target="_blank">Try Out</a></button>
                                        </div>
                                    </div>
                                </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}